@import url(https://fonts.googleapis.com/css2?family=Jost:wght@500&display=swap);
body {
  padding-top: 5%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background-color: #ffffff;
    font-family: "Jost", sans-serif;
    scroll-behavior: smooth;
}

h3 {
    color: #272929;
}

h5 {
    color: #FF552B;
}

h6 {
    color: #272929;
}

#section__container--s1 {
    color: #272929;
}

#section__container--s2 {
    color: #FF552B;
}

p {
    color: #272929;
    font-weight: 500;
}

a {
    color: #FF552B;
}

a:hover {
    color: #f13202;
}

#about__container--section {
    text-align: justify;
    -webkit-text-decoration: underline #FF552B;
            text-decoration: underline #FF552B;
}

@media only screen and (max-width: 600px) {
    #about__container--section {
    }
}

@keyframes mover{
    0%{
        transform: translate(100%);
    }
}
#menu {
    background-color: #272929;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
}

#menu__bienvenido {
    color: #ffffff;
    transition: 0.3s ease;
    text-decoration: none;
    background-color: #272929;
}

#menu__bienvenido:hover {
    transition: 0.3s ease;
    color: #272929;
    background-color: #ffffff;
}

#menu__item {
    background-color: ivory;
    color: #ffffff;
    transition: 0.3s ease;
    text-decoration: none;
    background-color: #272929;
}
#menu__item:hover {
    transition: 0.3s ease;
    background-color: #ffffff;
    color: #272929;
}

#about {
    padding: 3%;
}

#about__container {
    border-radius: 10px;
}


#enlace {
    text-decoration: none;
}

#button_a {
    background-color: #272929;
    color: #f0eaea;
}

#button_a:hover {
    background-color: #FF552B;
    color: #f0eaea;
}

#button_a:active {
    color: #f0eaea;
}

@media only screen and (max-width: 600px) {
    #about {
        padding: 5%;
        margin-top: 15%;
    }
}
#skills {
    padding: 3%;
    background-color: white;
}

#skills__container {
    border-radius: 10px;
}

#enlace {
    text-decoration: none;
}

#foto_idioma {
    width: 50%;
}

#foto_tecnologias {
