    height: 200px;
}

#projects__img {
    background-color: black;
    height: 180px;
    max-height: 180px;
}

#projects__img--b {
    background-color: white;
    height: 180px;
    max-height: 180px;
}

#enlace_title {
    color: #272929;
    text-decoration: none;
}

#enlace_title:hover {
